import React from "react";
import { motion } from 'framer-motion';
import Header from "./HeaderComp";

export default function About() {
    return (
		<motion.div initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}}>
			<Header />
			<main className="About">
				<h2>Hello, I'm Baptiste. I'm a frontend Developer from Strasbourg, in France.</h2>
				<picture>
					<img src="/img/IMG_2067.jpg" alt="Me" draggable="false" />
				</picture>
				<article>
					I am a front-end web developer specialized in SEO and UX/UI, living currently in Strasbourg, France. Where I have been learning web development for about 3 years.<br />
					I work from the user experience to the little visual details that I transform in well optmised code. <br />
					I like working on every type of websites. When I start to work on a project, I usualy end it all the way.
				</article>
			</main>
		</motion.div>
    );
}